.NavigationItems {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin: 0;
    -webkit-transition: 0.7s ease;
    -o-transition: 0.7s ease;
    transition: 0.7s ease;
}

.sidebar-toggle {
    margin: 20px;
    color: #333;
    font-size: xx-large;
}
.workButton {
    background: none;
    border: none;
    font-family: 'Calibre';
    font-size: 1.2rem;
    color: #333;
    text-decoration: none;
    height: 100%;
    margin-right: 16px;
}

.mobileMenue {
    position: fixed;
    background-color: #fff;
    /* background-image: linear-gradient(#fff, #f69c7e); */
    right: 0;
    top: 58px;
    gap: 0;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 60%;
    height: 100%;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    -ms-flex-align: center;
        align-items: center;
    /* box-shadow: -10px 0px 3px 0px #ccc; */
    -webkit-box-shadow: -10px 0px 3px 0px rgba(0,0,0,.2);
            box-shadow: -10px 0px 3px 0px rgba(0,0,0,.2);
}





