.Toolbar {
    height: 60px;
    width: 100%;
    /* margin: 16px 126px; */
    width: -webkit-fill-available;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
            box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    background-color: #fff;
    /* opacity: 80%; */
    position: fixed;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    /* padding: 0 20px; */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-image: #ccc;
    z-index: 90;
    color: #000
}

.Toolbar nav {
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.logo {
    height: 60px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: 126px;
}


/* in mobile device  */

@media (max-width :600px) {
    /* .DesktopOnly {
        display: none;
    }
    .Toolbar nav {
        display: none;
    } */

    .Toolbar {
        -ms-flex-align: center;
            align-items: center;
    }
    .logo {
        height: 60px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin-left: 0px;
    }
}


