@font-face {
    font-family: CuteTittle;
    src: url(../../assets/fonts/AbrilFatface-Regular.ttf);
  }


.wrapperOuter {
    margin: 126px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.aboutmeText{
    margin: 50px 126px;
    font-size: 1rem;
    text-align: justify;
}

.AboutMeTitle {
    font-family: CuteTittle !important;
}

.redColor {
    color: #f68d91;
}


/* Next project Button CSS */

.myResumeContainer {
    margin-top: 32px;
    margin-left: 0;
}
.aboutmePic {
  width: 300px;
}

span{
    position: relative;
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 240px;
    height: 55px;
    margin: 0 15px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
   
  }
  .MyResumeh a{
    font-size: 16px;
    letter-spacing: 1px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: translateZ(-25px);
            transform: translateZ(-25px);
    -webkit-transition: -webkit-transform .25s;
    transition: -webkit-transform .25s;
    -o-transition: transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
  }
  .MyResumeh a:before,
  .MyResumeh a:after{
    position: absolute;
    content: "Resume";
    height: 55px;
    width: 240px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border: 3px solid #333436;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 5px;
  }
  .MyResumeh a:before{
    color: #fff;
    background: #393E46;
    -webkit-transform: rotateY(0deg) translateZ(25px);
            transform: rotateY(0deg) translateZ(25px);
  }
  .MyResumeh a:after{
    color: #393E46;
    -webkit-transform: rotateX(90deg) translateZ(25px);
            transform: rotateX(90deg) translateZ(25px);
  }
  .MyResumeh a:hover{
    -webkit-transform: translateZ(-25px) rotateX(-90deg);
            transform: translateZ(-25px) rotateX(-90deg);
  }



@media (max-width : 500px) {
 

    .wrapperOuter {
        margin: 10px 12px;
    }

    /* .aboutmeText{
        margin: 32px 12px;
        font-size: 1.3rem;
    } */

    .aboutmePic {
        width: 200px;
    }
    .aboutmeText{
        margin: unset;
        font-size: 1.3rem;
        text-align: justify;

    }
    .aboutmePic {
      margin-bottom: 24px;
  }
}



